@import url('https://fonts.googleapis.com/css2?family=Cinzel&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Inconsolata&display=swap');

/* -------------------- global ------------------------ */
:root {
  /* --visited-color: rgb(21, 179, 0); */
  --paragraph-color: #1fadb8;
  --title-color: #f4b400;
  --link-color: white;
  --link-hover-color: yellow;
  --tab: 1.5rem;
  --heading-color: green;
  --background-color:rgb(0, 0, 0);
}
body {
  font-size: calc(var(--font-size) * 1.2);
  background-color: white;
  margin: 0;
  color: gray;
  font-family: 'Inconsolata', "Courier New", Courier, monospace;
}
.app {
  padding: 2rem calc(var(--font-size) * 8);
  background-color: var(--background-color);
  /* border-radius: 1rem; */
}

.mid-line {
  display: block;
  width: 100%;
  border-bottom: 0.15rem dashed white;
  line-height: 0.1em;
  margin: calc(var(--font-size) * 3) 0 0 0;
}
.mid-line span {
  color: var(--title-color);
  margin-left: calc(var(--font-size) * 2);
  padding: 0 var(--font-size);
  background-color: var(--background-color);
  text-transform: capitalize;
}
.link {
  text-decoration: none;
  color: var(--link-color);
  border-bottom: 1px dotted #f7f7f7;
}

/* ----------------------------------- blog --------------------------- */
.blogs {
  text-align: center;
  color:white;
}
.single-blog img {
  object-fit: cover;
  border-radius: 50%;
  margin: calc(var(--font-size) * 2) auto;
  display: block;
  height: calc(var(--font-size) * 10);
  width: calc(var(--font-size) * 10);
  max-width: 100%;
}
.single-blog {
  box-shadow: 4px 4px 4px 0 gray;
  /* border: 1px solid gray; */
  border-radius: 1rem;
  padding: var(--font-size);
  margin: calc(var(--font-size) * 5) 0rem;
  line-height: calc(var(--font-size) * 2);
}
.single-blog .title {
  font-family: monospace;
  font-weight: bolder;
  text-align: center;
  color: lightgreen;
}

/* ------------------------------- navbar ----------------------------------- */
nav {
  text-align: end;
  padding-bottom: 2rem;
}
nav ul {
  margin-bottom: calc(var(--font-size) * 2);
  margin-top: 0rem;
  padding: 0;
  text-indent: 0;
}
nav ul li {
  list-style: none;
  float: right;
  padding: 0.5rem;
}

/* ------------------------------- project ----------------------------------- */
.single-project {
  box-shadow: 4px 4px 4px 0 gray;
  color:white;
  border-radius: var(--font-size);
  padding: var(--font-size);
  margin: 2rem 0px;
  text-align: center;
  line-height: 1.5rem;
}
.single-project .title {
  color: lightgreen;
  font-weight: bolder;
}
.projects{
  /* display: flexbox; */
}

/* ------------------------------- home ------------------------------------- */
.home{
  text-align: end;
  color: white;
  font-family: 'Cinzel', 'Inconsolata', "Courier New", Courier, monospace;
  font-size: calc(var(--font-size) * 2);
}
