/* ------------------------------ about --------------------------------------- */
.about {
  /* margin: 0.5rem var(--tab); */
}
.about p {
  color: var(--paragraph-color);
  text-align: justify;
}
/* ------------------------------ contact ------------------------------- */
.contact p {
  color: var(--paragraph-color);
  text-align: justify;
  margin-bottom: calc(var(--font-size) * 3);
}
.label {
  color: lightgreen;
}
.form input,
.form textarea {
  border: 0;
  border-bottom: 2px solid gray;
  width: 100%;
  padding: 2px 0px;
  margin: calc(var(--font-size) * 0.5);
  font-family: inherit;
  outline: 0;
  font-size: var(--font-size) * 1.5;
  color: white;
  background-color: transparent;
  transition: border-color 0.2s;
}
.form input,
.form textarea :placeholder {
  color: transparent;
}
.form input:focus,
.form textarea:focus {
  padding-bottom: 2px;
  font-weight: 500;
  border-width: 2px;
  border-image: linear-gradient(to right, green, lightgreen);
  border-image-slice: 1;
}
.form .submit-button {
  font-size: var(--font-size) * 2;
  font-weight: 1000;
  height: 2rem;
  text-transform: capitalize;
  text-align: center;
  box-shadow: 4px 4px 4px 0 gray;
}
.form .submit-button:hover {
  background-color: rgb(71, 71, 71);
}

/* --------------- footer --------------------------- */
.footer{
  margin-bottom: 3rem;
  padding: 0.5rem;
  text-align: center;
}
.footer-link{
  padding: 0.5rem;
  margin: 0rem 2rem;
}

.link:hover {
  color: var(--link-hover-color);
}